import React from 'react';
import cards from './those-cards.png';
import './Home.css';

class GoButton extends React.Component {
  render() {
    return(
      <button
        className = { this.props.className }
        onClick = { this.props.onClick }
      >
        { this.props.text }
      </button>

    );
  }
}

class Home extends React.Component {

  constructor( props ) {
    super( props );
    this.handleClick = this.handleClick.bind( this );
    this.state = {
      aboutHidden: true
    };
  }

  handleClick = ( i ) => {
    if( i === 1 ) this.setState( state => ({ aboutHidden: !state.aboutHidden }));
    else this.props.changePage( i );
  }

  render() {

    const aboutHidden = this.state.aboutHidden;

    return (

      <div>

        <code>
          Early Access BTW v3.4.2.0
        </code>

        <div>
          <div className = "orbit">
            <GoButton
              text = { aboutHidden ? "Do not click" : "I warned you" }
              onClick = { () => this.handleClick( 1 ) }
              className = { "tellMore" }
            />
            <div hidden = { aboutHidden }>
              HELLO FRIENDS.
            </div>
          </div>

          <div>
            <b id = "preText">
              Tell Me More:
            </b>
            <GoButton
              text = { "About Me" }
              onClick = { () => this.handleClick( "about" ) }
              className = { "YYY" }
            />
          </div>
          <div className = "flashBG">
            <b id = "preText" className = "flashFG">
              Even More:
            </b>
            <GoButton
              text = { "Maybe A Blog Here" }
              onClick = { () => this.handleClick( "blog" ) }
              className = { "more" }
            />
            <GoButton
              text = { "Games" }
              onClick = { () => this.handleClick( "games" ) }
              className = { "more" }
            />
          </div>
        </div>

        <footer>
          <img src = { cards } alt = "cards" />
        </footer>

      </div>

    );

  }

}

export default Home;
