import { Route, Switch, /*Redirect,*/ BrowserRouter } from "react-router-dom";
import React from "react";
import { createHashHistory } from "history";
import Home from "./Home.js";
import About from "./About.js";
import Blog from "./Blog.js";
import Games from "./Games.js";
import Ribbon from "./Ribbon.js";
import YYYYYY from "./YYYYYY.js";

const Router = () => {

  let history = createHashHistory({
    forceRefresh: true,
  });

  const changePage = ( to ) => {

    console.debug( `navigating to ${to}` );
    console.debug( `Public URL:\n${process.env.PUBLIC_URL}`);

    switch( to ) {
      case "home":
        history.push( "/" );
        break;
      case "about":
        history.push( "/about" );
        break;
      case "blog":
        history.push( "/blog" );
        break;
      case "games":
        history.push( "/games" );
        break;
      case "YYYYYY":
        history.push( "/YYYYYY" );
        break;
      default:
        console.error( "Nice dude, good route" );
    }
  }

    //<BrowserRouter basename = { `${process.env.PUBLIC_URL}` } >

  return(
    <BrowserRouter basename = { "#" } >
      <Ribbon changePage = { ( to ) => changePage( to ) } />
      <Switch>
        <Route exact path = "/">
          <Home changePage = { ( to ) => changePage( to ) } />
        </Route>
        <Route exact path = "/about">
          <About changePage = { ( to ) => changePage( to ) } />
        </Route>
        <Route exact path = "/blog">
          <Blog changePage = { ( to ) => changePage( to ) } />
        </Route>
        <Route exact path = "/games">
          <Games changePage = { ( to ) => changePage( to ) } />
        </Route>
        <Route exact path = "/YYYYYY">
          <YYYYYY/>
        </Route>
        <Route path = "*">
          <h3>BruH This page don't exist</h3>
          <button onClick = { () => changePage( "home" ) } >Eject</button>
        </Route>
      </Switch>
    </BrowserRouter>
  );

};

export default Router;
